/* Main */

	#main {
		padding: 6em 0 4em 0 ;

		> .inner {
			> header.major:first-child {
				margin-bottom: 6em;
			}
		}

		@media screen and (max-width: 1280px) {
			padding: 4em 0 2em 0 ;

			> .inner {
				> header.major:first-child {
					margin-bottom: 4em;
				}
			}
		}

		@media screen and (max-width: 736px) {
			padding: 2.5em 0 0.5em 0 ;

			> .inner {
				> header.major:first-child {
					margin-bottom: 2.5em;
				}
			}
		}
	}