/* List */

	ol {
		list-style: decimal;
		margin: 0 0 2em 0;
		padding-left: 1.25em;

		li {
			padding-left: 0.25em;
		}
	}

	ul {
		list-style: disc;
		margin: 0 0 2em 0;
		padding-left: 1em;

		li {
			padding-left: 0.5em;
		}

		&.alt {
			list-style: none;
			padding-left: 0;

			li {
				border-top: solid 1px;
				padding: 0.5em 0;

				&:first-child {
					border-top: 0;
					padding-top: 0;
				}
			}
		}

		&.icons {
			list-style: none;
			padding-left: 0;

			li {
				display: inline-block;
				padding: 0 1em 0 0;

				&:last-child {
					padding-right: 0;
				}

				span {
					font-size: 1.1em;
					margin-left: 5px;
				}

				.icon {
					&:before {
						font-size: 1.1;
					}
				}
			}
		}

		&.bulleted-icons {
			display: flex;
			flex-wrap: wrap;
			padding: 0;

			li {
				margin: 2em 0 0 0;
				overflow: hidden;
				padding-left: 4.5em;
				position: relative;
				width: 50%;

				.icon-wrapper {
					font-size: 0.65em;
					left: 0;
					position: absolute;
					top: 0;

					.icon {
						&:before {
							font-size: 1rem;
						}
					}
				}

				h3 {
					font-size: 0.9em;
					margin-bottom: 0.5em;
					padding-bottom: 0;

					&:after {
						display: none;
					}
				}
			}

			@media screen and (max-width: 1280px) {
				li {
					width: 100%;
					margin: 1em 0 0 0;
				}
			}
		}

		&.actions {
			cursor: default;
			list-style: none;
			padding-left: 0;

			li {
				display: inline-block;
				padding: 0 1em 0 0;
				vertical-align: middle;

				&:last-child {
					padding-right: 0;
				}
			}

			&.small {
				li {
					padding: 0 0.5em 0 0;
				}
			}

			&.vertical {
				li {
					display: block;
					padding: 1em 0 0 0;

					&:first-child {
						padding-top: 0;
					}

					> * {
						margin-bottom: 0;
					}
				}

				&.small {
					li {
						padding: 0.5em 0 0 0;

						&:first-child {
							padding-top: 0;
						}
					}
				}
			}

			&.fit {
				display: table;
				margin-left: -1em;
				padding: 0;
				table-layout: fixed;
				width: calc(100% + 1em);

				li {
					display: table-cell;
					padding: 0 0 0 1em;

					> * {
						margin-bottom: 0;
					}
				}

				&.small {
					margin-left: -0.5em;
					width: calc(100% + 0.5em);

					li {
						padding: 0 0 0 0.5em;
					}
				}
			}

			@media screen and (max-width: 480px) {
				margin: 0 0 2em 0;

				li {
					padding: 1em 0 0 0;
					display: block;
					text-align: center;
					width: 100%;

					&:first-child {
						padding-top: 0;
					}

					> * {
						width: 100%;
						margin: 0 !important;

						&.icon {
							&:before {
								margin-left: -2em;
							}
						}
					}
				}

				&.small {
					li {
						padding: 0.5em 0 0 0;

						&:first-child {
							padding-top: 0;
						}
					}
				}
			}
		}

		&.menu {
			padding: 0;
			list-style: none;

			li {
				padding: 0;
				display: inline-block;
				margin-left: 0.75em;
				padding-left: 0.75em;
				border-left: solid 1px;
				line-height: 1em;

				&:first-child {
					margin-left: 0;
					padding-left: 0;
					border-left: 0;
				}
			}
		}
	}

	dl {
		margin: 0 0 2em 0;

		dt {
			display: block;
			font-weight: _font(weight-bold);
			margin: 0 0 1em 0;
		}

		dd {
			margin-left: 2em;
		}
	}

	ul.alt li {
		border-top-color: #e6e6e6;
	}

	ul.menu li {
		border-left-color: #e6e6e6;
	}

