/* Containers */

	.container {
		margin-left: auto;
		margin-right: auto;
	}

	.container {
		width: 80%;
	}

	@media screen and (max-width: 980px) {

		.container {
			width: 90%;
		}

	}

	@media screen and (max-width: 736px) {

		.container {
			width: 90%;
		}

	}

	@media screen and (max-width: 480px) {

		.container {
			width: 90%;
		}

	}
