/* Wrapper */

	.wrapper {
		padding: 4em 0 2em 0 ;
		position: relative;

		> .inner {
			margin: 0 auto;
			max-width: 100vw;
			width: 90%;
		}

		& + & {
			border-top: solid 1px #e6e6e6;
		}

		// Apply more padding than usual.
		&.major-pad {
			padding: 6em 0 4em 0 ;
		}

		// Apply padding to .content elements *inside* wrapper, instead of the wrapper itself.
		&.content-pad {
			padding: 0;

			.content {
				padding: 4em 0 2em 0 ;
			}
		}

		// Divide space evenly between two child elements.
		&.split {
			> .inner {
				display: flex;
				justify-content: space-between;

				> * {
					width: calc(50% - 3.75em);

					&:before {
						content: '';
						display: block;
						width: 1px;
						height: 100%;
						position: absolute;
						top: 0;
						margin-left: -3.75em;
						background-color: #e6e6e6;
					}

					&:first-child {
						border: 0;

						&:before {
							display: none;
						}
					}
				}
			}
		}

		&.style1 {
			background-color: #444;
			color: #fff;
			border-top: 0;

			& + .wrapper {
				border-top: 0;
			}

			&.split {
				> .inner {
					> * {
						&:before {
							background-color: rgba(255, 255, 255, 0.25);
						}
					}
				}
			}
		}

		&.style2 {
			background-color: #f6f6f6;
			color: #444;
			border-top: 0;

			& + .wrapper {
				border-top: 0;
			}

			&.split {
				> .inner {
					> * {
						&:before {
							background-color: #e6e6e6;
						}
					}
				}
			}
		}

		&.sidebar {
			> .inner {
				display: flex;
				flex-wrap: wrap;

				> header {
					width: 100%;
				}

				> .content {
					width: 75%;
					padding: 0 4em 0 0;
				}

				> .sidebar {
					width: 25%;
				}
			}

			&.right {
			}

			&.left {
				> .inner {
					flex-direction: row-reverse;

					> .content {
						padding: 0 0 0 4em;
					}
				}
			}

			@media screen and (max-width: 980px) {
				> .inner {
					flex-direction: row;

					> .content {
						width: 100%;
						padding: 0;
					}

					> .sidebar {
						border-top: solid 2px #e6e6e6;
						margin: 2em 0 0 0;
						padding: 4em 0 0 0;
						width: 100%;
					}
				}

				&.style1 {
					> .inner {
						> .sidebar {
							border-top-color: rgba(255, 255, 255, 0.25);
						}
					}
				}

				&.style2 {
					> .inner {
						> .sidebar {
							border-top-color: #e6e6e6;
						}
					}
				}
			}
		}

		@media screen and (max-width: 1280px) {
			padding: 3em 0 1em 0 ;

			> .inner {
				width: 100%;
				padding: 0 3em;
			}

			&.major-pad {
				padding: 4em 0 2em 0 ;
			}

			&.content-pad {
				.content {
					padding: 3em 0 1em 0 ;
				}
			}

			&.split {
				> .inner {
					> * {
						width: calc(50% - 2em);

						&:before {
							margin-left: -2em;
						}
					}
				}
			}
		}

		@media screen and (max-width: 980px) {
			> .inner {
				> * {
					width: 100%;
					padding-left: 0 !important;
				}
			}

			&.split {
				> .inner {
					flex-direction: column;

					> * {
						width: 100%;
						padding-top: 3em;
						margin-top: 1em;

						&:before {
							margin-left: 0;
							position: relative;
							height: 1px;
							width: 100%;
							top: -3em;
						}

						&:first-child {
							padding-top: 0;
							margin-top: 0;

							&:before {
								display: none;
							}
						}
					}
				}
			}
		}

		@media screen and (max-width: 736px) {
			padding: 2.5em 0 0.5em 0 ;

			> .inner {
				padding: 0 1.5em;
			}

			&.major-pad {
				padding: 2.5em 0 0.5em 0 ;
			}

			&.content-pad {
				.content {
					padding: 2.5em 0 0.5em 0 ;
				}
			}
		}
	}