/* Navigation Panel */

	#navPanel {
		transform: translateX(20em);
		transition: transform 0.5s ease, box-shadow 0.5s ease, visibility 0.5s;
		-webkit-overflow-scrolling: touch;
		background-color: _palette(bg);
		box-shadow: none;
		display: none;
		height: 100%;
		max-width: 80%;
		overflow-y: auto;
		position: fixed;
		right: 0;
		top: 0;
		visibility: hidden;
		width: _size(navPanel-width);
		z-index: _misc(z-index-base) + 2;
		font-family: _font(family-heading);
		font-weight: _font(weight-heading);
		text-transform: uppercase;

		ul {
			list-style-type: none;
		}

		nav {
			padding: 3em 2em;
		}

		.link {
			border: 0;
			border-top: solid 1px _palette(border);
			color: inherit !important;
			display: block;
			font-size: 0.9em;
			padding: 0.75em 0;

			&:first-child {
				border-top: 0;
			}

			&.depth-0 {
				font-weight: _font(weight-heading-bold);
				color: inherit !important;
			}

			.indent-1 { display: inline-block; width: 1.25em; }
			.indent-2 { display: inline-block; width: 2.5em; }
			.indent-3 { display: inline-block; width: 3.75em; }
			.indent-4 { display: inline-block; width: 5em; }
			.indent-5 { display: inline-block; width: 6.25em; }
		}

		.close {
			transition: color 0.2s ease-in-out;
			-webkit-tap-highlight-color: rgba(0,0,0,0);
			border: 0;
			color: _palette(fg-bold);
			cursor: pointer;
			display: block;
			height: 4em;
			padding-right: 1.25em;
			position: absolute;
			right: 0;
			text-align: right;
			top: 0;
			vertical-align: middle;
			width: 5em;

			&:before {
				content: '\f00d';
				font-family: FontAwesome;
				font-style: normal;
				font-weight: normal;
				text-transform: none !important;
				width: 3em;
				height: 3em;
				line-height: 3em;
				display: block;
				position: absolute;
				right: 0;
				top: 0;
				text-align: center;
			}

			&:hover {
				color: inherit;
			}

			@media screen and (max-width: 736px) {
				height: 4em;
				line-height: 4em;
			}
		}

		&.visible {
			transform: translateX(0);
			box-shadow: 0 0 0.125em 0 rgba(0,0,0,0.35);
			visibility: visible;
		}

		@media screen and (max-width: 980px) {
			display: block;
		}

		@media screen and (max-width: 736px) {
			display: block;

			nav {
				padding: 2.25em 1.25em;
			}
		}
	}