/* Icon */

	.icon {
		text-decoration: none;
		border-bottom: none;
		position: relative;
	}

		.icon:before {
			-moz-osx-font-smoothing: grayscale;
			-webkit-font-smoothing: antialiased;
			font-family: FontAwesome;
			font-style: normal;
			font-weight: normal;
			text-transform: none !important;
			font-size: 1.5rem;
		}

		.icon > .label {
			display: none;
		}

		.icon > img {
			width: 3em
		}

		.icon:before {
			cursor: inherit;
		}

	.icon-wrapper {
		width: 4.5em;
		height: 4.5em;
		line-height: 4.5em;
		display: inline-block;
		text-align: center;
		position: relative;
		margin: 1.235em 0;
		border-radius: 50%;
		color: #fff;
	}

		.icon-wrapper .icon {
			cursor: default;
		}

		.icon-wrapper.major {
			margin: 1.235em 0 3.235em 0;
			background-color: #7ebd26;
		}

		.icon-wrapper.small {
			font-size: 0.75em;
		}


	.icon-wrapper {
		background-color: #f6f6f6;
		box-shadow: 0 -1px 0 0 #f6f6f6, 0 1px 0 0 #f6f6f6;
	}
