/* Banner */

	@keyframes fadeIn {
		0% {
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}

	#banner {
		background-color: #323A45;
		color: #fff;
		min-height: 40em;
		height: 75vh;
		position: relative;

		&:after {
			pointer-events: none;
			background-image: url('/images/banners/png_920x620/image-1.png');
			filter: brightness(80%);
			background-attachment: fixed;
			background-size: cover;
  		background-repeat: no-repeat;
  		background-position:	center 0px;
			content: '';
			display: block;
			height: 100%;
			position: absolute;
			width: 100%;
			z-index: 0;
			opacity: 1;
			animation: fadeIn 2s ease;
		}

		.logo {
			background-image: url('/images/logos/cal-adapt_logo.svg');
			background-repeat: no-repeat;
			background-position: center;
			padding: 0.5em 1em;
			height: 5em;
		}


		> article {
			transition: opacity 1.5s ease-in-out, visibility 1.5s;
			background-attachment: fixed;
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;
			height: 100%;
			left: 0;
			position: absolute;
			text-align: center;
			top: 0;
			width: 100%;
			z-index: 1;

			&:before {
				content: '';
				display: inline-block;
				height: 100%;
				vertical-align: middle;
			}

			.inner {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				z-index: 1;

				> :last-child {
					margin-bottom: 0;
				}
			}

			h2 {
				//font-weight: _font(weight-heading-extrabold);
				font-size: 2.75em;
				padding-bottom: 0;
				margin: 0 0 0.5em 0;
				color: #fff;

				&:after {
					display: none;
				}
			}
		}

		@media screen and (max-width: 1280px) {
			min-height: 35em;
			height: 60vh;

			> article {
				h2 {
					font-size: 2.25em;
				}
			}
		}

		@media screen and (max-width: 980px) {
			min-height: 50em;
			height: 75vh;

			> article {
				background-attachment: scroll;
			}
		}

		@media screen and (max-width: 736px) {
			min-height: 25em;

			> article {
				.inner {
					width: 90%;
				}

				h2 {
					font-size: 1.75em;
					line-height: 1.25em;
				}
			}
		}

		body.is-mobile & {
			> article {
				background-attachment: scroll;
			}
		}
	}