/* Section/Article */

	section, article {
		&.special {
			text-align: center;
		}

		footer {
			border-top: solid 2px _palette(border);
			margin: 2em 2em 0;
			padding: 1em 0;
			display: flex;
			justify-content: center;
			flex-direction: column;

			p {
				margin: 0;
				color: _palette(fg-light);
			}

			.actions {
				margin-top: 1em;
			}


			@media (min-width: _breakpoint(medium)) {
				flex-direction: row;

				.actions {
					margin-top: 0;
					padding-left: 1em;
				}

			}

		}

	}

	header {
		p {
			color: _palette(fg-light);
			font-style: italic;
			margin: 0 1em 1.5em 1em;
			position: relative;
		}

		a {
			color: _palette(fg-light);
		}

		h2 + p {
			font-size: 1.25em;
			line-height: 1.5em;
			margin-top: -1em;
		}

		h3 + p {
			font-size: 1.1em;
			line-height: 1.5em;
			margin-top: -0.85em;
		}

		&.major {
			margin: 0 0 4em;
			text-align: center;

			> :last-child {
				margin-bottom: 0;
			}

			h2 {
				font-size: 2.5em;
				margin: 0 0 0.7em 0;
			}

			@media screen and (min-width: _breakpoint(large)) {
				margin: 0 4em 4em;

				h2 {
					font-size: 2.5em;
				}
			}

		}
	}