/* Footer */

	#footer {
		background: _palette(accent1, bg);
		color: _palette(accent1, fg);
		padding: 4em 0 2em 0 ;
		position: relative;

		> .inner {
			margin: 0 auto;
			max-width: 90em;
			position: relative;
			width: 90%;
		}

		.copyright {
			color: _palette(fg-light);
			font-size: 0.9em;
			text-align: center;
		}

		h3 {
			color: _palette(accent1, fg);
		}

		ul li a {
			border-bottom: none;
			color: _palette(accent1, fg);
		}


		@media screen and (min-width: _breakpoint(large)) {
			padding: 3em 0 1em 0 ;
			//text-align: center;

			.copyright {
				margin: 0 0 1em 0;
			}

			.menu {
				position: relative;
			}
		}
	}