/* Type */

	body {
		background-color: #fff;
		color: #444;
	}

	body, input, select, textarea {
		font-family: "Open Sans", Helvetica, sans-serif;
		font-size: 13pt;
		font-weight: 400;
		line-height: 1.65em;
	}

		@media screen and (max-width: 1680px) {

			body, input, select, textarea {
				font-size: 11pt;
			}

		}

	a {
		transition: border-bottom-color 0.2s ease-in-out;
		border-bottom: dotted 1px;
		text-decoration: none;

		&:hover {
			border-bottom-color: transparent;
		}
	}

	.Grid-cell > a {
		border-bottom: none;
	}

	strong, b {
		font-weight: _font(weight-bold);
	}

	em, i {
		font-style: italic;
	}

	p {
		margin: 0 0 2em 0;
	}

	h1, h2, h3, h4, h5, h6 {
		font-family: _font(family-heading);
		font-weight: _font(weight-heading-bold);
		line-height: 1em;
		margin: 0 0 1em 0;
		padding-bottom: 0;
		color: #444;

		span {
			font-weight: _font(weight-heading);
		}

		&:after {
			display: none;
		}

		a {
			border-bottom: 0;
			color: inherit;
			text-decoration: none;
		}

		&.alt {
			margin: 0 0 1 0;
			padding-bottom: 1.25em;

			&:after {
				//display: none;
				border-bottom: solid 1px;
				bottom: 0;
				content: '';
				display: inline-block;
				left: 0;
				position: absolute;
				width: 3rem;
			}
		}
	}

	h2 {
		font-size: 2em;
		line-height: 1.5em;
	}

	h3 {
		font-size: 1.35em;
		line-height: 1.5em;
	}

	h4 {
		font-size: 1.1em;
		line-height: 1.5em;
	}

	h5 {
		font-size: 0.9em;
		line-height: 1.5em;
	}

	h6 {
		font-size: 0.7em;
		line-height: 1.5em;
	}

	sub {
		font-size: 0.9em;
		position: relative;
		//top: 0.5em;

		a {
			color: inherit;
		}
	}

	sup {
		font-size: 0.9em;
		position: relative;
		top: -0.5em;
	}

	blockquote {
		border-left: solid 4px #7ebd26;
		font-style: italic;
		margin: 0 0 2em 0;
		padding: 0.5em 0 0.5em 2em;
	}

	code {
		border: solid 1px;
		font-family: _font(family-fixed);
		font-size: 0.9em;
		margin: 0 0.25em;
		padding: 0.25em 0.65em;
	}

	pre {
		-webkit-overflow-scrolling: touch;
		font-family: _font(family-fixed);
		font-size: 0.9em;
		margin: 0 0 2em 0;

		code {
			display: block;
			line-height: 1.75em;
			padding: 1em 1.5em;
			overflow-x: auto;
		}
	}

	hr {
		border: 0;
		border-bottom: solid 1px;
		margin: 2em 0;

		&.major {
			margin: 3em 0;
		}
	}

	.align-left {
		text-align: left;
	}

	.align-center {
		text-align: center;
	}

	.align-right {
		text-align: right;
	}

	input, select, textarea {
		color: #444;
	}

	a {
		color: #444;
	}

	strong, b {
		color: #444;
	}

	code {
		background: #f6f6f6;
		border-color: #e6e6e6;
	}

	hr {
		border-bottom-color: #e6e6e6;
	}