/* Overrides for default styles in noUiSlider module  */

.range-slider.noUi-background {
  background: #ccc;
  height: 4px;
  border: none;
  box-shadow: none;

  .noUi-handle {
    width: 14px;
    height: 14px;
    left: 0;
    border: none;
    border-radius: 50%;
    background: goldenrod;
    cursor: pointer;
    box-shadow: none;

    &:after {
      display: none;
    }

    &:before {
      display: none;
    }
  }
}

.range-slider .noUi-background {
  background: #ccc;
  border: none;
  box-shadow: none;
}

