// Misc.
	$misc: (
		z-index-base:		10000
	);

// Duration.
	$duration: (
		nav:				0.5s,
		transition:			0.2s
	);

// Breakpoints
	$breakpoint: (
		small : 24em,
		medium : 46.8em,
		large : 50em,
		xlarge : 73em
	);

// Size.
	$size: (
		navPanel-width:		20em,
		element-height:		2.75em,
		element-margin:		2em
	);

// Font.
	$font: (
		family:						('Open Sans', Helvetica, sans-serif),
		family-heading:				('Open Sans Condensed', sans-serif),
		family-fixed:				('Courier New', monospace),
		weight:						400,
		weight-bold:				600,
		weight-heading:				300,
		weight-heading-bold:		700
	);

// Palette.
	$palette: (
		bg:					#fff,
		fg:					#444,
		fg-bold:			#444,
		fg-light:			#888888,
		border:				#e6e6e6,
		border-bg:			#F0F2F4,
		border2:			#a6a6a6,
		border2-bg:			#e2e2e2,
		highlight:			accent1,
		box:				transparent,

		accent1: (
			bg:				#323A45,
			fg:				#fff,
			fg-bold:		#fff,
			fg-light:		#888,
			border:			rgba(255,255,255,0.25),
			border-bg:		rgba(255,255,255,0.075),
			border2:		rgba(255,255,255,0.5),
			border2-bg:		rgba(255,255,255,0.2),
			highlight:		accent1,
			box:			transparent
		),

		accent2: (
			bg:				#F0F2F4,
			fg:				#444,
			fg-bold:		#444,
			fg-light:		#bbb,
			border:			#e6e6e6,
			border-bg:		rgba(144,144,144,0.075),
			border2:		#a6a6a6,
			border2-bg:		rgba(144,144,144,0.2),
			highlight:		accent1,
			box:			solid
		)
	);