.Media {
  display: flex;
  align-items: flex-start;
  margin-bottom: 3em;
  flex-direction: column;
}

.Media-figure {
  margin-right: 1em;
  margin-bottom: 1em;
}

.Media-figure--small {
  width: 5em;
}

.Media-body {
  flex: 1;
}
.Media-body,
.Media-body :last-child {
  margin-bottom: 0;
}

.Media-body p {
  margin: 0 0 0.75em;
}

.Media-title {
  margin: 0 0 .5em;
}

.Media--center {
  align-items: center;
}

.Media--reverse > .Media-figure {
  order: 1;
  margin: 0 0 0 1em;
}

@media (min-width: _breakpoint(medium)) {
  .Media {
    flex-direction: row;
  }
}