/* Spotlight */

	.spotlight {
		display: flex;
		justify-content: space-between;

		.content {
			width: calc(33.3333333% - 1.25em);
		}

		h2 {
			font-size: 2.25em;
			margin: 0 0 0.7em 0;
			padding-bottom: 0.75em;
		}

		.image {
			width: calc(66.6666666% - 2.5em);
			margin: 0 0 2em 0;

			img {
				width: 100%;
			}
		}

		@media screen and (max-width: 1280px) {
			h2 {
				font-size: 1.25em;
			}

			.image {
				width: calc(66.6666666% - 1.25em);
			}
		}

		@media screen and (max-width: 980px) {
			display: block;

			.content {
				width: 100%;
			}

			.image {
				width: 100%;
				margin: 0 0 2em 0;
			}
		}
	}