/* Posts */

	.posts {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: stretch;

		.post {
			width: calc(33.33333333% - 1.25em);
		}

		@media screen and (max-width: 1280px) {
			.post {
				width: calc(50% - 1em);
			}
		}

		@media screen and (max-width: 736px) {
			display: block;

			.post {
				width: 100%;
			}
		}
	}