/* Post */
.Post {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #f6f6f6;
	margin-bottom: 2em;
}

.Post-image {
	flex: 1;
	width: 100%;
	max-width: 300px;
	max-height: 100px;
}

.Post-title {
	flex: 1;
}

.Post-body {
	flex: 2;
	padding: 0.5em;
}

@media (min-width: _breakpoint(medium)) {
	.Post {
		flex-direction: row;
	}

	.Post-image {
		max-height: 150px;
		height: 150px;
		width: 250px;
		-webkit-clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
		clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
	}

	.Post-title {
		padding: 0.5em;
	}
}

@media (min-width: _breakpoint(large)) {
	.Post-image {
		max-height: 200px;
		height: 200px;
		width: 300px;
	}
}

