.modal-overlay
{
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;

  opacity: 0;

  width: 100%;
  height: 100%;

  transition: 1ms opacity ease;

  background: rgba(0,0,0,.7);

  pointer-events: none;
}

.modal
{
  position: fixed;
  z-index: 9999;
  top: 50%;
  left: 50%;

  opacity: 0;

  width: 94%;
  padding: 24px 20px;


  transition: 1ms opacity ease;
  transform: translate(-50%, -50%);

  border-radius: 2px;
  background: #fff;
}

.modal.modal-open.modal-anchored
{
  top: 20px;
  transform: translate(-50%, 0);
}

.modal.modal-open
{
  opacity: 1;
}

.modal-overlay.modal-open
{
  opacity: 1;

}

/* Close Button */

.modal-close
{
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 2em;
  outline: none;
  border: none;
  line-height: 1;
  box-shadow: none;
  font-weight:  _font(weight-heading);
  padding: 0 0.1em;
  margin: 0 0.1em;
  height: auto;
}


/* Default Animation */

.modal-overlay.zoom
{
    display: block;
    opacity: 0;
}

.modal.zoom
{

    -webkit-transition: 500ms -webkit-transform ease;
       -moz-transition: 500ms    -moz-transform ease;
        -ms-transition: 500ms     -ms-transform ease;
         -o-transition: 500ms      -o-transform ease;
            transition: 500ms         transform ease;
    -webkit-transform: translate(-50%, -50%) scale(0);
       -moz-transform: translate(-50%, -50%) scale(0);
        -ms-transform: translate(-50%, -50%) scale(0);
         -o-transform: translate(-50%, -50%) scale(0);
            transform: translate(-50%, -50%) scale(0);

    opacity: 1;

    display: block;
}

.modal.zoom.modal-open
{
    -webkit-transition: 500ms -webkit-transform 500ms ease;
       -moz-transition: 500ms    -moz-transform 500ms ease;
        -ms-transition: 500ms     -ms-transform 500ms ease;
         -o-transition: 500ms      -o-transform 500ms ease;
            transition: 500ms         transform 500ms ease;
    -webkit-transform: translate(-50%, -50%) scale(1);
       -moz-transform: translate(-50%, -50%) scale(1);
        -ms-transform: translate(-50%, -50%) scale(1);
         -o-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}

.modal.zoom.modal-open.modal-anchored
{
    -webkit-transition: 500ms -webkit-transform 500ms ease;
       -moz-transition: 500ms    -moz-transform 500ms ease;
        -ms-transition: 500ms     -ms-transform 500ms ease;
         -o-transition: 500ms      -o-transform 500ms ease;
            transition: 500ms         transform 500ms ease;
}

.modal-overlay.zoom.modal-open
{
    top: 0;

    -webkit-transition: 500ms opacity ease;
       -moz-transition: 500ms opacity ease;
        -ms-transition: 500ms opacity ease;
         -o-transition: 500ms opacity ease;
            transition: 500ms opacity ease;

    opacity: 1;
}

.modal-overlay.zoom
{
    -webkit-transition: 500ms opacity 500ms ease;
       -moz-transition: 500ms opacity 500ms ease;
        -ms-transition: 500ms opacity 500ms ease;
         -o-transition: 500ms opacity 500ms ease;
            transition: 500ms opacity 500ms ease;
}

.modal ul {
  list-style: none;
}

.modal .download-button {
    margin: 10px 0;
    width: 80%;
    text-decoration: none; /* 6 */
    position: relative; /* 8 */
    transition: 0.15s; /* 9 */
}

.modal .download-button::after {
  content: ''; /* 1 */
  background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/2037/download.svg') no-repeat center; /* 2 */
  position: absolute; /* 3 */
  top: 0; /* 4 */
  bottom: 0; /* 4 */
  right: 0; /* 5 */
  width: 80px; /* 6 */
}

.modal .download-button:focus,
.modal .download-button:hover {
  //border-color: $medium-gray; /* 1 */
  outline: none; /* 2 */
}

.modal .download-button:active {
  animation: enlight 0.5s;
}

@keyframes enlight {
  to { box-shadow: 0 0 0 8px #fce8ed; }
}

.modal .download-options-list {
    display: flex;
    flex-direction: column;
}




