// Radio button groups - default
.options-grp {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;

  @media (min-width: _breakpoint(medium)) {
    & {
      flex-direction: row;
    }
  }

  .button {
    flex: 1 1 calc(33% - 10px);
    max-width: 100%;
    display: flex;
    align-items: center; 
    justify-content: center;
    text-align: center;
    height: auto;
    margin: 10px 15px 10px 0; 
    padding: 10px;
    border: 1px solid rgba(0,0,0,.07);
    box-shadow: 0 5px 10px rgba(0,0,0,.07);

    &:last-of-type {
      margin-right: 0;
    }

    label {
      font-size: 1.2em;
      display: flex;
      flex-direction: column;
      font-weight:  _font(weight-heading);
      font-family:  _font(family-heading);
      padding: 0;
      margin: 0;
      white-space: normal;
      max-width: 100%;

    }

    label:before {
      display: none;
    }

    label .title {
      font-size: 1.2em;
      line-height: 1.5;
      //display: block;
    }

    label .sub-title {
      font-size: 0.8em;
      line-height: 1.1;
      //display: block;
    }
  }

  .button:hover {
    opacity: 1;
  }

  .button.active {
    background:  _palette(border);
    opacity: 1;
  }

  .icon {
    padding: 10px;
  }

  .cb {
    flex: 1 1 calc(20% - 10px);
    margin: 5px 7px 5px 1px; 
    opacity: 0.4;
    color: #fff;
    padding: 0;

    label {
      margin: 0;
      color: #fff;
      text-shadow: 1px 1px 1px #767676;
    }

    input[type=checkbox]+label:before {
      border: none;
      background-color: rgba(0,0,0,0.3);
    }

    input[type=checkbox]:checked+label:before {
      border: none;
      color: #fff;
      background-color: rgba(0,0,0,0.3);
    }
  }

}
