.boundary .inner {
  display: flex;
  flex-wrap: wrap;

  .location-type-options {
    z-index: 101;
    flex: 1 1 100%;
    position: relative;

    select {
      margin-bottom: 2em;
    }
    
  }

  .map-pane {
    padding: 10px;
    border: 3px solid _palette(border);
    height: 350px;
    flex: 1 1 calc(50% - 2em);
    z-index: 101;
    position: relative;
  }

  .location-details {
    height: 350px;
    flex:1 1 calc(50% - 2em);
    text-align: center;
    z-index: 101;
    position: relative;

    h2 {
      margin-top: 1em;
      margin-bottom: 0;
    }
  }

  .location-list {
    flex: 1 1 100%;
    margin-top: 2em;
    position: relative;

    .location-list-heading {
      margin-top: 1em;
    }
  }

  .coordinate-input {

    display: flex;
    justify-content: flex-start;
    flex-flow: row nowrap;
    align-items: center;
    margin-bottom: 1em;

    p {
      margin: 0 10px 0 0;
    }

    input {
      max-width: 100px;
      margin-right: 10px;
    }
  }

  .expanded-content {
    width: 100%;
    height: 0;
    overflow: hidden;
    pointer-events: none;
    opacity: 0;
  }
}

// Overlay for boundary
.boundary-overlay {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: _palette(border-bg);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s;
  transition-timing-function: cubic-bezier(0.7,0,0.3,1);
}

.boundary.open {
  position: absolute;
  top: 1em;

  .boundary-overlay {
    opacity: 1;
  }

  .hide {
    display: none;
  }

  .expanded-content {
    opacity: 1;
    height: auto;
    overflow: visible;
    transition: opacity 0.3s 0.5s;
    z-index: 101;
    pointer-events: all;
  }

  .location-details {
    display: none;
  }

  .location-list {

    .location-list-heading table thead {
      background-color: rgba(0,0,0,0.2);
    }

    .location-list-heading table th {
      text-transform: uppercase;
      text-align: center;
      padding: 0.5em 0 0.5em;
    }

    .location-list-body {
      max-height:300px; 
      overflow:auto;
    }

    .location-table tr {
      display: none;
      text-align: center;
      cursor: pointer;

      &.no-result {
        display: none;
      }
      &.visible {
        display: table-row;
      }
       &:hover {
        background: rgba(0,0,0,0.05);
      }

      &.active {
        background: rgba(126,5,153,0.2);
      }
    }

  }
  
}

.leaflet-pelias-control.hide {
  display: none;
}